import { ComponentChildren, Fragment } from 'preact';
import { ItemContext } from './Collection.types';

/**
 * Props for the Collection Component
 */
export type Props<D> = {
  /**
   * An array of data
   */
  items: D[];
  /**
   * A function to render each item
   */
  children: (context: ItemContext<D>) => ComponentChildren;
};

/**
 * Component that renders an array of data.
 */
export function Collection<D>({ items, children }: Props<D>) {
  return (
    <Fragment>
      {items.map((data: D, index: number) => {
        let ctx = { index, data };
        return children(ctx);
      })}
    </Fragment>
  );
}
